.modal {
  position: fixed;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  top: 0;
  left: 0;
  background-color: rgba(104, 94, 94, 0.412);
}
.warning {
  z-index: 4;
  position: fixed;
  width: 60vw;
  max-height: 400px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  top: 50%;
  left: 50%;
  padding: 0;
  padding-top: 10px;
  border-radius: 20px;
  padding-left: 15px;
  padding-right: 15px;
  background-color: #fff;
  transform: translate(-50%, -50%);
  margin: auto;
  overflow-y: scroll;
  padding-top: 40px;
}
